import eski from './assets/eski.jpg';
import yeni from './assets/yeni.jpg';
import './App.css';
import React, {useEffect, useState} from "react";
import {Bubble} from "./components/Bubble";

function App() {

    const [random, setRandom] = useState(0);
    const [numbers, setNumbers] = useState([1, 2, 3, 4, 5]);
    const [isVisible, setVisible] = useState(false);
    const [title, setTitle] = useState("Olmadı!");

    useEffect(() => {
        numberGenerator();
    }, [])

    function numberGenerator() {
        let temp = Math.round(Math.random() * 5);
        if (temp === 0) {
            temp = 1;
        }
        setRandom(temp);
    }

    function renderButtons() {
        return numbers.map((item, index) => {
            return (
                <button
                    onClick={() => {
                        if (item === random) {
                            setTitle("Tebrikler");
                            setVisible(true);
                        } else {
                            setVisible(true);
                            // setTimeout(() => setVisible(false), 800)
                            let temp = [...numbers];
                            temp.splice(index, 1);
                            setNumbers(temp);
                        }
                    }}
                    style={{padding: 20, margin: "0 2px"}}>{item}</button>
            )
        })
    }

    return (
        <div className="App">
            <header className="App-header">
                <div style={{position: "relative"}}>
                    <img src={yeni} className="App-logo" alt="logo"/>
                    {isVisible &&
                    <Bubble title={title}/>
                    }
                </div>
                <h2 style={{color: "#00aaee"}}>Merhaba,</h2>
                <p style={{color: "#9f00ff"}}>
                    Ben Fatih ÖZER, Nedim Turan İlkokulu 2A Sınıfı öğrencisiyim.
                </p>
                <p style={{color: "#fa3b21"}}>
                    Teknolojiye meraklı olduğum için böyle bir websitem olsun istedim.
                </p>
                {title === "Olmadı!" &&
                <>
                    <p style={{color: "#1e1997"}}>1 ve 5'e arasında bir sayı tuttum. Tahmin et bakalım?</p>

                    <div>
                        {renderButtons()}
                    </div>
                </>
                }

            </header>
        </div>
    );
}

export default App;
